button {
    color: white;
    background-color: orangered;
    border-radius: 15px;
    width:15vh;
    border: none;
    height:5vh;
    cursor: pointer;
}

.db button{
    left: 45%;
    bottom: 50%;
    position: fixed;
    width: 10%;
    background-image: linear-gradient(to right, #11429d 0%, #f22957 51%, #11429d 100%);
    animation: placeholderShimmer 10s linear 0s infinite normal forwards;
    cursor: pointer;
    display: none;
}

.df {
    background-color: #EDF5FF;
}

@media screen and (max-width: 768px) {
    .db button
    {
        display: block;
        width:40%;
        left:30%;
        bottom: 2%;
    }
}